.snext-toast {
    position: fixed;
    left: 24px;
    bottom: 40px;
    width: 300px;
    background: #46101A;
    color: #fff;
    padding: 16px 24px;
    box-shadow: 0 4px 24px #0006;
    opacity: 1;
    pointer-events: auto;
    transition: opacity 0.3s;
    z-index: 9999;
    font-size: 16px;
    font-weight: 500;
    border-left: 6px solid #F23C5B;
    display: flex;
    align-items: center;
    gap: 12px;
}

.snext-toast.hide {
    opacity: 0;
    pointer-events: none;
}


@media (max-width: 700px) {
    .snext-toast {
        top: 24px;
        bottom: auto;
        left: 50%;
        right: auto;
        transform: translateX(-50%);
        width: 96vw;
        max-width: 350px;
    }
}