body {
  margin: 0;
  font-family: "Segoe UI", Arial, sans-serif;
  background: #181c24;
  color: #fff;
  scrollbar-width: thin;
  scrollbar-color: #48488a #181c24;
}

body.sidebar-open,
.SporeNEXT.sidebar-open {
  overflow: hidden !important;
  height: 100vh !important;
  position: fixed;
  width: 100vw;
}

html,
.SporeNEXT {
  scrollbar-width: thin;
  scrollbar-color: #48488a #181c24;
}

html::-webkit-scrollbar,
.SporeNEXT::-webkit-scrollbar {
  width: 10px;
  background: #181c24;
}

html::-webkit-scrollbar-thumb,
.SporeNEXT::-webkit-scrollbar-thumb {
  background: #48488a;
  border-radius: 8px;
}

html::-webkit-scrollbar-thumb:hover,
.SporeNEXT::-webkit-scrollbar-thumb:hover {
  background: #5a5abf;
}

body,
.SporeNEXT {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

a {
  color: #14a0e1;
  text-decoration: none;
  transition: color 0.2s, text-decoration 0.2s;
  font-weight: 500;
  word-break: break-all;
}

a:hover,
a:focus {
  color: #50c3f8;
  text-decoration: underline;
  outline: none;
}

img {
  -webkit-user-drag: none;
  pointer-events: auto;
}

.SporeNEXT {
  min-height: 100vh;
  background: #111722 url("./assets/wallpaper.png") no-repeat center center
    fixed;
  background-size: cover;
  position: relative;
  z-index: 0;
}

.SporeNEXT::before {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(
    to top,
    #111722 0%,
    #111722 70%,
    rgba(16, 16, 34, 0.95) 100%
  );
  z-index: 1;
  pointer-events: none;
}

.SporeNEXT > * {
  position: relative;
  z-index: 2;
}

.snext-navbar {
  width: 100%;
  height: 56px;
  background: #1d1d37;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 32px;
  box-sizing: border-box;
  box-shadow: 0 2px 8px #0002;
  position: sticky;
  top: 0;
  z-index: 10;
}

.snext-navbar-left {
  display: flex;
  align-items: center;
  gap: 10px;
}

.snext-logo {
  width: 30px;
  height: 30px;
  margin-right: 20px;
  transition: transform 0.6s cubic-bezier(0.4, 0, 0.2, 1);
}

.snext-logo:hover {
  animation: snext-logo-spin 3s linear infinite;
}

@keyframes snext-logo-spin {
  100% {
    transform: rotate(360deg);
  }
}

.snext-navbar-link {
  color: #fff;
  text-decoration: none !important;
  font-weight: 600;
  font-size: 1.2em;
  padding: 8px 12px;
  border-radius: 6px;
  transition: background 0.15s;
  cursor: pointer;
}

.snext-navbar-link:hover {
  background: #2e2e57;
}

.snext-navbar-link.active,
.snext-navbar-link.active:hover {
  background: #2e2e57;
  color: #14a0e1;
}

.snext-caret {
  font-size: 0.8em;
  margin-left: 2px;
}

.snext-navbar-right {
  display: flex;
  align-items: center;
  gap: 16px;
}

.snext-navbar-switch {
  font-size: 0.95em;
  opacity: 0.8;
}

.snext-btn {
  font-weight: 600;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  padding: 8px 18px;
  cursor: pointer;
  transition: background 0.15s, color 0.15s;
  margin-left: 2px;
}

.snext-btn-blue {
  background: #14a0e1;
  color: #181c24;
}

.snext-btn-blue:hover {
  background: rgb(80, 195, 248);
}

.snext-avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: #23283a;
  border: 2px solid #23283a;
  margin-left: 8px;
}

.snext-main {
  display: flex;
  margin-top: 24px;
  max-width: 1600px;
}

.snext-sidebar {
  position: sticky;
  align-self: flex-start;
  height: fit-content;
  top: 80px;
  width: 220px;
  background: #1d1d37;
  padding: 15px 15px 0 15px;
  margin-left: 20px;
  margin-top: 80px;
  margin-bottom: 30px;
  border-radius: 16px;
  z-index: 2;
}

.snext-section {
  margin-bottom: 32px;
}

.snext-section-title {
  font-size: 1.1em;
  font-weight: bold;
  margin-bottom: 10px;
  color: #b3b8c5;
}

.snext-section-item {
  padding: 10px;
  border-radius: 6px;
  color: #b3b8c5;
  cursor: pointer;
  font-size: 1em;
  margin-bottom: 2px;
  transition: background 0.15s, color 0.15s;
}

.snext-section-item.active,
.snext-section-item:hover {
  color: #fff;
  background: #2e2e57;
}

.snext-section-title-collapsible {
  color: #fff;
  border-radius: 10px;
  padding: 15px;
  font-weight: bold;
  font-size: 1.15em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
  transition: background 0.15s;
}

.snext-section-title-collapsible:hover {
  background: #39396d;
}

.snext-content {
  flex: 1 1;
  padding: 30px 32px 32px 60px;
}

.snext-content-header {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  gap: 24px;
  margin-bottom: 18px;
  font-size: 1.1em;
  color: #b3b8c5;
}

.snext-sort-btn {
  background: #23283a;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 12px 16px;
  font-size: 0.9em;
  cursor: pointer;
  transition: background 0.15s;
}

.snext-sort-btn:hover {
  background: #35357a;
}

.snext-cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 10px;
}

.snext-card {
  background: #1d1d37;
  border-radius: 16px;
  padding: 18px;
  box-shadow: 0 4px 24px #0004;
  display: grid;
  grid-template-rows: 1fr auto;
  position: relative;
  transition: background 0.3s cubic-bezier(0.4, 0, 0.2, 1), box-shadow 0.2s,
    transform 0.2s;
}

.snext-card:hover {
  background: #23234a;
  box-shadow: 0 8px 32px #0006;
  transform: translateY(-4px) scale(1.02);
}

.snext-card-main {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex: 1 1 auto;
}

.snext-card-bottom {
  margin-top: 0;
}

.snext-card-img {
  width: 100%;
  height: 120px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3.5em;
  font-weight: bold;
  margin-bottom: 16px;
  color: #fff;
}

.snext-card-img img {
  max-width: 100%;
  max-height: 100%;
  width: auto;
  height: auto;
  display: block;
  margin: 0 auto;
  border-radius: 10px;
  box-shadow: none;
}

.snext-card-img-r {
  background: #2e3a4a;
}

.snext-card-img-bepinex {
  background: #3a2e2e;
}

.snext-card-title {
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 4px;
  color: #fff;
}

.snext-card-author {
  font-size: 0.95em;
  color: #14a0e1;
  margin-bottom: 8px;
}

.snext-card-author span {
  color: #14a0e1;
}

.snext-card-desc {
  font-size: 0.805rem;
  color: #b3b8c5;
}

.snext-card-tags {
  margin-bottom: 0px;
}

.snext-tag {
  display: inline-block;
  background: #181c24;
  color: #14a0e1;
  font-size: 0.85em;
  padding: 3px 10px;
  border-radius: 6px;
  margin-top: 10px;
  margin-right: 6px;
  font-weight: 600;
}

.snext-card-row {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0;
  margin-bottom: 2px;
}

.snext-card-meta {
  font-size: 1em;
  color: #b3b8c5;
  display: flex;
  gap: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  align-items: center;
}

.snext-card-meta span {
  display: flex;
  align-items: center;
  gap: 4px;
}

.snext-card-meta span .svg-inline--fa {
  transition: transform 0.2s;
}

.snext-card-meta span:hover .svg-inline--fa {
  transform: scale(1.3);
}

.snext-card-timestamp {
  font-size: 0.9em;
  color: #b3b8c5;
  display: flex;
  gap: 16px;
  margin: 0;
  padding: 0;
  align-items: center;
}

.snext-sort-menu {
  position: absolute;
  top: 110%;
  right: 0;
  background: #23234a;
  border-radius: 12px;
  box-shadow: 0 4px 24px #0006;
  min-width: 210px;
  z-index: 20;
  padding: 8px 0;
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.snext-sort-menu-item {
  color: #fff;
  font-size: 0.9em;
  padding: 12px 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 12px;
  border: none;
  background: none;
  transition: background 0.13s, color 0.13s;
}

.snext-sort-menu-item:hover,
.snext-sort-menu-item.active {
  background: #35357a;
}

.snext-sort-menu-icon {
  font-size: 1.2em;
  width: 24px;
  display: inline-block;
  text-align: center;
}

.snext-logout-btn {
  color: #fff;
  transition: background 0.15s, color 0.15s;
}

.snext-logout-btn:hover {
  color: #14a0e1;
}

.snext-logout-btn:hover .svg-inline--fa {
  color: #14a0e1;
}

.snext-mobile-navbar {
  display: none;
}

@media (max-width: 1100px) {
  .snext-main {
    flex-direction: column;
    margin-top: 16px;
    max-width: 100vw;
  }

  .snext-sidebar {
    position: static;
    width: 100%;
    margin: 0 0 24px 0;
    padding: 24px 16px 0 16px;
    border-radius: 0 0 16px 16px;
    top: unset;
    z-index: 1;
  }

  .snext-content {
    padding: 24px 8px 24px 8px;
  }

  .snext-cards {
    grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
    gap: 16px;
    min-height: 400px;
  }
}

@media (max-width: 700px) {
  .SporeNEXT {
    background: none;
    position: static;
  }

  .snext-navbar {
    display: none !important;
  }

  .snext-navbar-left,
  .snext-navbar-right {
    flex-direction: column;
    gap: 8px;
    width: 100%;
    align-items: flex-start;
  }

  .snext-navbar-link,
  .snext-btn {
    width: 100%;
    text-align: left;
    padding: 10px 12px;
    font-size: 1em;
  }

  .snext-main {
    margin-top: 8px;
  }

  .snext-sidebar {
    width: 98vw;
    max-width: 340px;
    min-width: 0;
    margin: 12px auto 16px auto;
    border-radius: 12px;
    background: #181a2f;
    box-shadow: 0 2px 12px #0004;
    position: static;
    top: unset;
    z-index: 2;
  }

  .snext-section {
    margin-bottom: 14px;
  }

  .snext-section-title {
    font-size: 1.08em;
    margin-bottom: 10px;
    font-weight: bold;
    color: #fff;
  }

  .snext-section-item {
    font-size: 0.97em;
    padding: 7px 8px;
    margin-bottom: 2px;
    border-radius: 6px;
  }

  .snext-content {
    padding: 8px 2vw 16px 2vw;
  }

  .snext-content-header {
    flex-direction: column;
    align-items: stretch;
    gap: 12px;
    margin-bottom: 18px;
  }

  .snext-content-header > * {
    width: 100%;
    justify-content: flex-start;
  }

  .snext-sort-btn {
    width: 100%;
    justify-content: flex-start;
    font-size: 1em;
    padding: 12px 18px;
    border-radius: 10px;
    margin-top: 4px;
  }

  .snext-sort-menu {
    min-width: unset;
    width: 100%;
    left: 0;
    right: 0;
  }

  .snext-cards {
    grid-template-columns: 1fr;
    gap: 16px;
  }

  .snext-card {
    height: auto;
    min-height: unset;
    max-height: unset;
  }

  .snext-mobile-navbar {
    display: flex;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 65px;
    background: #1d1d37;
    border-top: 1px solid #23234a;
    z-index: 100;
    justify-content: space-around;
    align-items: center;
    box-shadow: 0 -2px 12px #0006;
  }

  .snext-mobile-navbar button {
    background: none;
    border: none;
    color: #b3b8c5;
    font: inherit;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 1.1em;
    gap: 2px;
    cursor: pointer;
    flex: 1;
    height: 100%;
    justify-content: center;
    transition: color 0.2s;
  }

  .snext-mobile-navbar button img,
  .snext-mobile-navbar button .svg-inline--fa {
    width: 25px !important;
    height: 25px !important;
    display: block;
    margin: 0 auto;
  }

  .snext-mobile-navbar button:active,
  .snext-mobile-navbar button:focus {
    color: #14a0e1;
    outline: none;
  }

  .snext-mobile-navbar span {
    font-size: 0.85em;
    margin-top: 2px;
  }
}

@media (max-width: 500px) {
  .snext-navbar {
    padding: 4px 2vw;
  }

  .snext-logo {
    margin-right: 8px;
    width: 24px;
    height: 24px;
  }

  .snext-card {
    padding: 10px;
  }

  .snext-card-img {
    height: 80px;
    font-size: 2em;
  }
}

@media (max-width: 400px) {
  .snext-sidebar {
    max-width: 320px;
  }
}
