.snext-sidebar-menu {
    display: none;
}

.snext-sidebar-menu.open {
    display: block;
    position: fixed;
    inset: 0;
    z-index: 2000;
}

.snext-sidebar-menu-overlay {
    position: fixed;
    inset: 0;
    background: #0008;
    z-index: 1;
}

.snext-sidebar-menu-panel {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    width: 65vw;
    max-width: 340px;
    background: #181a2f;
    color: #fff;
    z-index: 2;
    padding: 32px 24px 24px 24px;
    box-shadow: 2px 0 16px #0006;
    display: flex;
    flex-direction: column;
    animation: slideInSidebar 0.25s;
}

.snext-sidebar-menu-close {
    background: none;
    border: none;
    color: #fff;
    font-size: 2em;
    position: absolute;
    top: 5px;
    left: 10px;
    cursor: pointer;
}

.snext-sidebar-menu-panel ul {
    list-style: none;
    padding: 0;
    margin: 48px 0 0 0;
}

.snext-sidebar-menu-panel li {
    margin: 0;
    font-size: 1.15em;
    cursor: pointer;
    padding: 20px;
    border-radius: 6px;
}

.snext-sidebar-menu-panel li:hover {
    background: #2e2e57;
}

@keyframes slideInSidebar {
    from {
        transform: translateX(-100%);
    }

    to {
        transform: translateX(0);
    }
}

@media (max-width: 700px) {
    .snext-sidebar-menu {
        display: none;
    }

    .snext-sidebar-menu.open {
        display: block;
    }
}